
import {Vue, Options} from 'vue-class-component';
import NavItem from './NavItem.vue';

@Options({
    components: {
        NavItem
    },
    props: {
        pages: Array
    }
    
})

export default class Nav extends Vue {
    pages!: Array<String>

    link(event: Event, l:String)  {
        console.log('clicked!');
        this.$emit('moveToLink', event, l);
    }
}

